import { FormBuilder } from "@ginkgo-bioworks/react-json-schema-form-builder";
import "bootstrap/dist/css/bootstrap.min.css";
import "./styles.scss";
import { useCallback, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
const mods = {
  newElementDefaultDataOptions: {
    title: "Input",
    type: "string",
    default: "",
  },
};
function App() {
  const location = useLocation();
  const [mode, setMode] = useState("dark");
  const [json, setJson] = useState({
    schema: "",
    uischema: "",
  });

  useEffect(() => {
    try {
      const sch = decodeURIComponent(
        new URLSearchParams(window.location.search).get("schema")
      );
      const mode = decodeURIComponent(
        new URLSearchParams(window.location.search).get("mode")
      );
      setMode(mode);
      const parsed = JSON.parse(sch ?? "");
      setTimeout(() => {
        const open = ({ labels, i }) => {
          if (i >= labels.length) {
            if (i > 0) {
              setTimeout(() => {
                labels[i - 1].click();
              }, 5);
            }
            return;
          }
          setTimeout(() => {
            if (i > 0) {
              setTimeout(() => {
                labels[i - 1].click();
              }, 5);
            }
            labels[i].click();
            open({ labels, i: i + 1 });
          }, 10);
        };
        const labels = document.getElementsByClassName("label");
        open({ labels, i: 0 });
      }, 100);
      setJson({
        schema: JSON.stringify(parsed?.schema ?? ""),
        uischema: JSON.stringify(parsed?.uiSchema ?? ""),
      });
    } catch (e) {
      console.log({ e });
    }
  }, [location]);

  useEffect(() => {
    if (mode) {
      const root = document.documentElement;
      root.style.setProperty(
        "--primary-backgroud",
        mode === "light" ? "#f1f1f1" : "#202641"
      );
      root.style.setProperty(
        "--dark-backgroud",
        mode === "light" ? "#ffffff" : "#181d35"
      );
      root.style.setProperty(
        "--light-text",
        mode === "light" ? "#181d35" : "#9aabc9"
      );
    }
  }, [mode]);

  useEffect(() => {
    json.schema && window.top.postMessage(JSON.stringify(json), "*");
  }, [json]);

  const handleChange = useCallback((newSchema, newUiSchema) => {
    setJson({
      schema: newSchema,
      uischema: newUiSchema,
    });
  }, []);

  return (
    <div className={`form-builder ${mode}`}>
      <FormBuilder
        schema={json.schema}
        uischema={json.uischema}
        onChange={handleChange}
        mods={mods}
      />
    </div>
  );
}

export default App;
